@font-face {
  font-family: 'aspira-regular';
  src: url('aspira-regular.eot');
  src:
    url('https://s3-eu-west-1.amazonaws.com/mcx-cdn/font-aspira/0.0.1/aspira-regular.eot?#iefix') format('embedded-opentype'),
    url('https://s3-eu-west-1.amazonaws.com/mcx-cdn/font-aspira/0.0.1/aspira-regular.woff') format('woff'),
    url('https://s3-eu-west-1.amazonaws.com/mcx-cdn/font-aspira/0.0.1/aspira-regular.ttf') format('truetype');
    /* url('https://s3-eu-west-1.amazonaws.com/mcx-cdn/font-aspira/0.0.1/aspira-regular.svg#aspira-regular') format('svg'); */
  font-weight: normal;
  font-style: normal;
}

