.aib-typography {
  font-family: 'aspira-regular', sans-serif;
}

.aib-typography .aib-typography--display3 {
  font-size: 2em;
  font-family: 'aspira-thin', sans-serif;
  line-height: 2.2em;
}
.aib-typography .aib-typography--display2 {
  font-size: 1.8em;
  line-height: 2em;
}
.aib-typography .aib-typography--display1 {
  font-size: 1.6em;
  line-height: 1.8em;
}
.aib-typography .aib-typography--accordion-title {
  font-size: 1.4em;
  line-height: 1.6em;
  /*color: $aib-corepurple-clr;*/
}
.aib-typography .aib-typography--accordion-content {
  font-size: 1.2em;
  line-height: 1.4em;
}
.aib-typography .aib-typography--page-title {
  font-family: 'aspira-demi', sans-serif;
  /*color: $aib-darkpurple-clr;*/
  font-size: 1.8em;
  line-height: 2em;
}
.aib-typography .aib-typography--headline {
  font-size: 1.6em;
  line-height: 1.8em;
}
.aib-typography .aib-typography--body1 {
  font-size: 1.2em;
  line-height: 1.8em;
}
.aib-typography .aib-typography--tab-active {
  font-size: 1.6em;
  line-height: 1.8em;
  font-family: 'aspira-demi', sans-serif;
}
.aib-typography .aib-typography--tab-inactive {
  font-size: 1.6em;
  line-height: 1.8em;
  font-family: 'aspira-thin', sans-serif;
}
.aib-typography .aib-typography--terms-conditions {
  font-size: 0.8em;
  line-height: 1em;
}
.aib-typography .aib-typography--caption {
  font-size: 0.6em;
}
.aib-typography .aib-typography--input-message--approved {
  font-size: 0.7em;
  /*color: $aib-darkgreen-clr;*/
}
.aib-typography .aib-typography--input-message--error {
  font-size: 0.7em;
  /*color: $aib-red-clr;*/
}
